<template>
  <a-layout>
    <a-layout-content style="min-width: 1200px;margin-right: auto;margin-left: auto;padding: 0 50px">
      <a-breadcrumb style="margin: 16px 0">
        <a-breadcrumb-item>首页</a-breadcrumb-item>
        <a-breadcrumb-item>企业资质</a-breadcrumb-item>
      </a-breadcrumb>
      <div :style="{ background: '#fff', padding: '24px', minHeight: '280px' }">
        <a-row :gutter="24" type="flex" justify="right">
          <a-col :span="3" offset="3">
            <a-card :bordered="false">
              <div class="account-center-avatarHolder">
                <div class="avatar">
                  <img src="../../assets/dddb-webEnt/company1.png" />
                </div>
              </div>
            </a-card>
          </a-col>
          <a-col :span="11">
            <a-form id="components-form-demo-validate-other" :form="form">
              <a-form-item label="在建工程名称" :labelCol="{ span: 6 }" :wrapperCol="{ span: 14 }">
                <span class="ant-form-text">{{entInfo.nowProject}}</span>
              </a-form-item>
              <a-form-item label="经营范围" :labelCol="{ span: 6 }" :wrapperCol="{ span: 14 }">
                <span class="ant-form-text">{{entInfo.scope}}</span>
              </a-form-item>
              <a-form-item label="企业介绍" :labelCol="{ span: 6 }" :wrapperCol="{ span: 14 }">
                <p class="ant-form-text">{{entInfo.enterDes}}</p>
              </a-form-item>
            </a-form>
          </a-col>
          <a-col :span="4" offset="1">
            <router-link to="/entBasicInfo">修改基本信息</router-link>
          </a-col>
        </a-row>
        <a-divider />
        <a-row  :gutter="24" type="flex" justify="right" align="middle" >
          <a-col :span="4" offset="3">
            <h2>企业资质信息</h2>
          </a-col>
          <a-col :span="2">
            <p>{{entInfo.authState}}</p>
          </a-col>
          <a-col :span="8">
            <a-alert v-if="entInfo.authState=='未提交'" message="请提交相关的企业资质信息,只有通过必填资料才能发布用工需求噢" type="info" showIcon />
            <a-alert v-if="entInfo.authState=='审核中'" message="您好！预计24h内审核完成 感谢您的等待 如有问题请咨询 0371-55635805" type="info" showIcon />
            <a-alert v-if="entInfo.authState=='未通过'" :message="entInfo.authAdvice" type="error" showIcon />
            <p v-if="entInfo.authState=='已通过'">评价：{{entInfo.authAdvice}}</p>
          </a-col>
          <a-col :span="4" offset="1">
            <router-link to="/entQualification">修改资质信息</router-link>
          </a-col>
        </a-row>

        <a-divider>
          <h3 style="width:124px;
            line-height: 40px;
            color: white;
            height:42px;
            background:rgba(184,12,10,1);
            box-shadow:0px 2px 20px 0px rgba(171,171,171,0.5);
            border-radius:21px;">
            必填
          </h3>
        </a-divider>

        <a-row type="flex" justify="center">
          <a-col :span="6">
            <a-alert message="只有通过必填资料才能发布用工需求噢" type="info" showIcon />
          </a-col>
        </a-row>
        <a-row type="flex" justify="start" style="background-color: rgba(237,237,237,1);margin-top: 20px">
          <a-col :span="6" offset="3" >
            <h2 style="font-weight: bold;margin-bottom: 40px;margin-top: 40px">企业名称:{{entInfo.enterName}}</h2>
          </a-col>
        </a-row>
        <a-row type="flex" justify="center" style="background-color: rgba(237,237,237,1); padding: 40px">
          <a-col :span="18">
            <div class="card-list" ref="content">
              <a-list rowKey="id" :grid="{gutter: 96, column: 3}" :dataSource="requiredData">
                <a-list-item slot="renderItem" slot-scope="item">
                  <template>
                    <a-card :hoverable="true">
                      <img alt="example" v-bind:src="item.imgSource" slot="cover" />
                      <template class="ant-card-actions" slot="actions">
                        <a style="color: #0c0c0c;">{{item.name}}</a>
                      </template>
                    </a-card>
                  </template>
                </a-list-item>
              </a-list>
            </div>
          </a-col>
        </a-row>

        <a-divider>
          <h3 style="width:124px;
          line-height: 40px;
          color: white;
          height:42px;
          background:rgba(184,12,10,1);
          box-shadow:0px 2px 20px 0px rgba(171,171,171,0.5);
          border-radius:21px;">
            选填
          </h3>
        </a-divider>
        <a-row type="flex" justify="center" style="background-color: rgba(237,237,237,1); padding: 40px">
          <a-col :span="18">
            <div class="card-list" ref="content">
              <a-list rowKey="id" :grid="{gutter: 96, column: 3}" :dataSource="selectedData">
                <a-list-item slot="renderItem" slot-scope="item">
                  <template>
                    <a-card :hoverable="true" style="position: relative;">
                      <img v-bind:alt="item.name" v-bind:src="item.imgSource" slot="cover"/>

                      <!-- <span style="position: absolute; top: 60px; left: 100px; padding: 0; margin: 0; color: #FFFFFF ">添加文字</span> -->
                      <template class="ant-card-actions" slot="actions">
                        <h3 style="color: #0c0c0c;">{{item.name}}</h3>
                      </template>
                    </a-card>
                  </template>
                </a-list-item>
              </a-list>
            </div>
          </a-col>
        </a-row>

      </div>
    </a-layout-content>
    <a-layout-footer style="text-align: center">DDDB ©2019 Created by 六芒星</a-layout-footer>
  </a-layout>
</template>

<script>
import {getEnterpriseInfoById} from '../../api/manRequest'
import globle from '../../api/global_varialble'
export default {
  components: {},
  data() {
    return {
      imgUrl: globle.baseURL,
      entId: Number(localStorage.getItem('entId')),
      entInfo:{},
    };
  },
  computed: {
    requiredData(){
      let list = [
        {
          name: "营业执照",
          link: this.entInfo.businessLicense,
          imgSource: this.imgUrl+ this.entInfo.businessLicense
        },
        {
          name: "建筑资质",
          link:this.entInfo.constructionQUAL,
          imgSource: this.imgUrl+ this.entInfo.constructionQUAL
        },
        {
          name: "安全生产许可证",
          link:this.entInfo.securityLicense,
          imgSource: this.imgUrl+ this.entInfo.securityLicense
        },
        {
          name: "社保缴费证明",
          link:this.entInfo.socialSecurityCert,
          imgSource: this.imgUrl+ this.entInfo.socialSecurityCert
        },
        {
          name: "拟用工项目中标通知书或其他文件",
          link:this.entInfo.noticeOfBid,
          imgSource: this.imgUrl+ this.entInfo.noticeOfBid
        },
        {
          name: "项目商业保险",
          link:this.entInfo.businessItemInsurance,
          imgSource: this.imgUrl+ this.entInfo.businessItemInsurance
        },
        {
          name: "无纳税异常声明",
          link:this.entInfo.noTaxExpStatement,
          imgSource: this.imgUrl+ this.entInfo.noTaxExpStatement
        }
      ];
      for (let index = 0; index < list.length; index++) {
        const element = list[index];
        if (element.link == null) {
          element.imgSource = 'static/D-qualification-cardbg.png'
        }
        else if (element.link != null) {
          var imgLink = (element.link).toString()
          if (imgLink.indexOf(".pdf") != -1) {
            element.imgSource = 'static/D-qualification-uploaded.jpeg'
          }else {
            element.imgSource = this.imgUrl+element.link;
          }
        }
      }
      return list;
    },
    selectedData(){
      let list = [
        {
          name: "土地使用证",
          link:this.entInfo.landUseCert,
          imgSource: this.imgUrl+ this.entInfo.landUseCert
        },
        {
          name: "规划许可证",
          link:this.entInfo.planningPermit,
          imgSource: this.imgUrl+ this.entInfo.planningPermit
        },
        {
          name: "施工许可证",
          link:this.entInfo.constructionPermit,
          imgSource: this.imgUrl+ this.entInfo.constructionPermit
        },
        {
          name: "开工报告",
          link:this.entInfo.startReport,
          imgSource: this.imgUrl+ this.entInfo.startReport
        }
      ];
      for (let index = 0; index < list.length; index++) {
        const element = list[index];
        if (element.link == null) {
          element.imgSource = 'static/D-qualification-cardbg.png'
        }
        else if (element.link != null) {
          var imgLink = (element.link).toString()
          if (imgLink.indexOf(".pdf") != -1) {
            element.imgSource = 'static/D-qualification-uploaded.jpeg'
          }else {
            element.imgSource = this.imgUrl+element.link;
          }
        }
      }
      return list;
    }
  },
  created() {
    // console.log(this.entId)
  },
  mounted() {
    this.$parent.showMenu=true
    this.getEnterpriseInfoById()
  },
  methods: {
    getEnterpriseInfoById(){
      getEnterpriseInfoById(this.entId)
      .then(response => {
        this.entInfo = response.data.data;
        this.thisimgSource = response.data.data.businessLicense;
        // console.log(this.entInfo);
        // console.log(this.imgUrl+this.thisimgSource);
      })
      .catch(err => {
        console.log(err);
      })
    }
  }
};
</script>

<style lang="less" scoped>
.account-center-avatarHolder {
  text-align: center;
  margin-bottom: 24px;

  & > .avatar {
    margin: 0 auto;
    width: 104px;
    height: 104px;
    margin-bottom: 20px;
    border-radius: 50%;
    overflow: hidden;
    img {
      height: 100%;
      width: 100%;
    }
  }

  .username {
    color: rgba(0, 0, 0, 0.85);
    font-size: 20px;
    line-height: 28px;
    font-weight: 500;
    margin-bottom: 4px;
  }
}

.account-center-detail {
  p {
    margin-bottom: 8px;
    padding-left: 26px;
    position: relative;
  }
}

.card-list {
  /deep/ .ant-card-body:hover {
    .ant-card-meta-title > a {
      color: #f5222d;
    }
  }

  /deep/ .ant-card-meta-title {
    margin-bottom: 12px;

    & > a {
      display: inline-block;
      max-width: 100%;
      color: rgba(0, 0, 0, 0.85);
    }
  }

  /deep/ .meta-content {
    position: relative;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    height: 64px;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;

    margin-bottom: 1em;
  }
}

.card-avatar {
  width: 48px;
  height: 48px;
  border-radius: 48px;
}

.ant-card-actions {
  background: #f7f9fa;

  li {
    float: left;
    text-align: center;
    margin: 12px 0;
    color: rgba(0, 0, 0, 0.45);
    width: 50%;

    &:not(:last-child) {
      border-right: 1px solid #e8e8e8;
    }

    a {
      color: rgba(0, 0, 0, 0.45);
      line-height: 22px;
      display: inline-block;
      width: 100%;
      &:hover {
        color: #f5222d;
      }
    }
  }
}

.new-btn {
  background-color: #fff;
  border-radius: 2px;
  width: 100%;
  height: 188px;
}

.vcenter {
  position: absolute;
  left: 50%;
  top: 50%;
}

</style>



